<template>
  <div class="settings">
    <h3>Server Info</h3>
    <h4>Current Address: {{ $store.getters.ip }}</h4>
    <label>IP Address:
      <input type="number" min="0" max="255" v-for="(section, $index) in temp.ip" :key="$index" v-model="temp.ip[$index]">
    </label>
    <br>
    <label>Port: <input type="number" min="0" max="65535" v-model="temp.port"></label>
    <div v-show="ipChanged">
      <button @click="updateIP">Update</button>
      <button @click="resetServer">Reset</button>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'settings',
  components: {
    HelloWorld
  },
  data() {
    return {
      temp: {
        ip: [],
        port: 0
      }
    }
  },
  methods: {
    resetServer() {
      while(this.temp.ip.length) this.temp.ip.pop();                                                                      // Use pop() for Vue reactivity
      this.$store.state.server.ip.forEach(section => { this.temp.ip.push(section); });
      this.temp.port = this.$store.state.server.port;
    },
    updateIP() {
      this.$store.commit('UPDATE_IP', { ip: this.temp.ip, port: this.temp.port });
    }
  },
  created() {
    this.resetServer();
  },
  computed: {
    ipChanged() {
      let diffs = this.temp.ip.filter((elem, $index) => { return (elem != this.$store.state.server.ip[$index] )});
      if (this.temp.port != this.$store.state.server.port) diffs.push(0);
      return (diffs.length > 0);
    }
  }
}
</script>